*{
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #fafafa;
    font-size: 16px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', 
    monospace;
}

.btn{
    display: inline-block;
    padding: 16px 12px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    background: lavender;
    text-align: center;
    outline: none;
}
.btn:hover{
    background: lightgrey;
}

.btn-long{
    width: 100%;
}

.btn-icon{
    padding: 8px;
    border: none;
    border-radius: 0;
    background: transparent;
    line-height: 1;
}
.btn-icon>span{
    font-size: 2rem;
    color: #aaa;
}

.btn-icon:hover{
    background: transparent;
    color: #333;
}
.btn-icon:hover>span{
    color: #333;
}

.btn-dark{
    background: #222;
    color: #fff;
}
.btn-dark:hover{
    background: #111;
    color: #fff;
}

.btn-success{
    background: lawngreen;
}
.btn-danger{
    background: tomato;
}
.btn-disabled{
    cursor: default;
    pointer-events: none;
}
.btn-disabled{
    cursor: not-allowed;
    pointer-events: none;
    opacity: .75;
}

.btn-list{
    margin-bottom: 16px;
}
.btn-list > .btn{
    margin-bottom: 8px;
}

.words-list{
    margin-top: 12px;
}
.words-list>.list-item{
    padding: 0px 0px 0px 4px;
    margin-bottom: 8px;
    line-height: 1;
}



.container{
    max-width: 380px;
    padding: 0 12px;
    margin: 0 auto;
    background-color: white;
}

.container-flex{
    display: flex;
    flex-direction: column;
}

.greedy{
    display: flex;
    flex-direction: column;
    flex-grow: 10;
    justify-content: space-between;
}

.breadcrumbs{
    margin: 0px 0 16px;
}

.title-main{
    margin: 8px auto 36px;
    font-size: 1.6rem;
}

.list, .game{
    padding: 0px 12px 24px;
    min-height: 100vh;
}

.list-header{
    padding: 0 0 12px;
}

.list-footer{
    padding: 12px 0 24px;
}

.list .btn{
    margin-bottom: 12px;
}

.question{
    margin: 0 auto;
    width: 100%;
    text-align: center;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.question-text{
    margin: 24px 12px 36px;
    font-weight: bold;
    font-size: 2rem;
}
.question>.btn-icon>span{
    font-size: 1.6rem;
}

.favorites-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #333;
}
.favorites-item:last-child{
    border: none;
}
.favorites-item>.btn-icon{
    font-size: 1.5rem;
    margin: 0;
}
.favorites-item>.btn-icon>span{
    font-size: 1.5rem;
}

.footer{
    font-size: .9rem;
    text-align: center;
}

.log{
    position: absolute;
    font-size: 10px;
    background: coral;
    padding: 2px;
    display: block;
    top: 0;
    right: 0;
    margin: auto;
}

.counter{
    text-align: center;
    color: #333;
    font-size: .9rem;
}

.text-danger{
    color: tomato !important;
}
